var that;
export default {
	data() {
		return {
			imglist: [],
			id: ''
		};
	},
	methods: {
		formSubmit(e) {
			var that = this;
			var content = e.detail.value.content;
			var id = that.id;
			var imglist = that.imglist;
			if (!content) {
				uni.showToast({
					title: '请输入举报内容',
					icon: 'none',
					duration: 1000
				});
				return false;
			}
			that.$request
				.request({
					url: that.api.live.report,
					method: 'POST',
					data: {
						id: id,
						content: content,
						image: imglist
					}
				})
				.then(res => {
					console.log(res);
					uni.showToast({
						title: '举报成功',
						icon: 'success',
						duration: 1000
					});
					setTimeout(function() {
						uni.navigateBack({
							delta: 1
						});
					}, 1200);
				});
			// .catch(() => console.log('promise catch err')); //捕获异常
		},
		async choose_img_upload(n) {
			// let name = e.currentTarget.dataset.name;
			let uploader = new this.$Uploader();
			let path_arr = await uploader.choose_and_upload(n);
			console.log('图片', path_arr);
			// that[name] = path_arr[0];
			this.imglist = this.imglist.concat(path_arr[0]);
		},
		DelImg(e) {
			var that = this;
			var index = e.currentTarget.dataset.index;
			uni.showModal({
				title: '提示',
				content: '确定要删除该图片吗？',
				cancelText: '取消',
				confirmText: '确定',
				success(res) {
					if (res.confirm) {
						console.log(index);
						console.log(typeof []);
						that.imglist.splice(index, 1);
					}
				}
			});
		}
	},
	onLoad(option) {
		that = this;
		console.log(option);
		that.id = option.id;
	}
};
